<template lang="pug">
	header.c-header
		.container
			.row.align-items-center
				.c-header__col-logo
					<app-logo/>
				.c-header__col-menu
					<app-burger/>
				.c-header__col-change-template
					<app-change-template/>
				.c-header__add-features
					.b-header-add-features
						.b-header-add-features__item
							<app-auth/>
						.b-header-add-features__item
							<app-Language/>
</template>
<!--SCRIPT-->
<script>
import AppLogo from '../blocks/AppLogo'
import AppBurger from '../blocks/AppBurger'
import AppChangeTemplate from '../blocks/AppChangeTemplate'
import AppAuth from '../blocks/AppAuth'
import AppLanguage from '../blocks/AppLanguage'
export default {
	name: 'AppHeader',
	components: {
		AppLogo,
		AppBurger,
		AppChangeTemplate,
		AppAuth,
		AppLanguage
	}
}
</script>

<style lang="sass">
	.b-header-add-features
		display: flex
		align-items: center
		justify-content: flex-end
		&--center
			justify-content: center
		&__item
			padding-right: 2rem
			&:last-child
				padding-right: 0

		@media screen and (min-width: $lg)

			&__item
				padding-right: 3.5rem
</style>



