<template lang="pug">
	div(:class="[custom ? 'o-hero-slider o-hero-slider--' + custom : 'o-hero-slider']")
		slick(ref="slick" :options="slickOptions")
			slot(name="item")

</template>
<script>
import Slick from 'vue-slick'
export default {
	props: ['custom'],
	components: {
		Slick
	},
	data () {
		return {
			slickOptions: {
				dots: true,
				infinite: true,
				speed: 800,
				slidesToShow: 1,
				arrows: false
			}
		}
	}
}
</script>
<style lang="sass">
	.o-hero-slider
		.slick-dots
			width: auto
			top: 50%
			left: 0
			bottom: inherit
			+translate(0, -50%)
			li
				display: flex
				align-items: center
				justify-content: center
				width: 25px
				height: 25px
				margin: 1rem 0
				+border-radius(50%)

				button
					font-size: 0
					width: 5px
					height: 5px
					padding: 0
					background-color: #d6d6d6
					+border-radius(50%)
					&:before
						display: none

				&.slick-active
					border: 1px solid $color-accent
					button
						width: 9px
						height: 9px
						background-color: $color-accent
		&--business
			.slick-dots
				left: inherit
				right: 0
			@media screen and (min-width: $lg)
				.slick-dots
					top: 4rem
					+translate(0,0)
</style>



