<template lang="pug">
	div(:class="['b-steps__item', custom]")
		span(:class="[captionCustom ?  'b-steps__caption b-steps__caption--' + captionCustom : 'b-steps__caption']")
			slot(name="caption")
		p.b-steps__desc
			slot(name="text")
</template>
<script>
export default {
	props: ['caption', 'text', 'custom', 'captionCustom']
}
</script>
<style lang="sass">
	.b-steps
		&__caption
			font-size: 2.2rem
			margin-bottom: .7rem
			display: block
			&--custom
				font-size: 3.6rem
				font-weight: 500
			&--custom-weight
				font-weight: 500
		&__caption-small
			font-size: 1.4rem
		&__desc
			font-size: 1.3rem
			margin-bottom: 0

		&__item
			flex: 1
			padding: 1rem 1.4rem 1.3rem
			margin-right: 1rem
			max-width: 20rem
			+transition(all, .4s, ease)
			&:last-child
				margin-right: 0
			&.active
				background-color: $color-accent
				.b-steps__caption
					font-weight: 800
</style>



