<template lang="pug">
	.b-online-payments-b
		.b-online-payments-b__heading
			h1.e-h1 {{ $t("Payments")}} <span class="e-h1__small e-h1__small--block e-h1__small--mt">{{ $t("with merchant from MoneyFGE")}}</span>
		.b-online-payments-b__form
			.row
				.col-lg-6.col-md-10
					<app-go-merchant/>
</template>
<script>
import AppGoMerchant from './AppGoMerchant'
export default {
	components: {
		AppGoMerchant
	}

}
</script>
<style lang="sass">
	.b-online-payments-b
		display: flex
		flex-direction: column
		margin-bottom: 6rem
		&__heading
			text-align: center
			margin-bottom: 4rem
			.e-h1
				line-height: 1
				margin-bottom: 0
		&__form
			margin-left: auto
			margin-right: auto
		@media screen and (min-width: $sm)
			&__heading
				text-align: left
			&__form
				margin-left: 0
				margin-right: 0

		@media screen and (min-width: $xl)
			margin-bottom: 8.5rem
			&__heading
				margin-bottom: 6.6rem
</style>



