<template lang="pug">
	form(v-on:submit.prevent="submitSubcr").b-main-form
		span(v-if="successfully").b-main-form__successfully {{$t("Thanks for subscribing")}}
		.b-main-form__row
			.b-main-form__input-focus.b-main-form__input-focus--in-row
				input(type="email", :placeholder="$t('Email')" v-model="email" required).b-main-form__input.b-main-form__input--sm
			button.button.button--sm {{$t("Subscribe")}}
</template>
<script>
export default {
	data () {
		return {
			email: '',
			successfully: false,
			errors: []

		}
	},
	methods: {
		submitSubcr: function () {
			submitSubcr(this.email)
		}
	}
}
function submitSubcr (email) {
	var payload = JSON.stringify({
		id: '1',
		email: email,
		date: {
			date: '2019-03-01 16:05:00.056732',
			timezone_type: 'int(3)',
			timezone: 'Europe/Istanbul'
		},
		status: 'privat',
		user: 'privat'
	})
	console.log(payload)
	sendApiReq('\g' + CONFIG_API_ENDPOINT + 'subscribe', 'POST', payload).then(
		function (resp) {
			if (resp.id) {
				console.log(true)
			} else {
				console.log(false)
			}
		},
		function (err) {
			console.log(false)
		}
	)
}
</script>



