<template lang="pug">
	.b-phone-slider
		.b-phone-slider__body
			<app-iphone-svg/>
		.b-phone-slider__slides.js-phone-slider
			slick(ref="slick" :options="slickOptions")
				.b-phone-slider__slide
					img(src="@/assets/img/phone-slider/item-1.png" alt="img")
				.b-phone-slider__slide
					img(src="@/assets/img/phone-slider/item-1.png" alt="img")
				.b-phone-slider__slide
					img(src="@/assets/img/phone-slider/item-1.png" alt="img")
				.b-phone-slider__slide
					img(src="@/assets/img/phone-slider/item-1.png" alt="img")
</template>
<script>
import Slick from 'vue-slick'
import AppIphoneSvg from './AppIphoneSvg'
export default {
	components: {
		AppIphoneSvg,
		Slick
	},
	data () {
		return {
			slickOptions: {
				dots: true,
				infinite: true,
				speed: 800,
				slidesToShow: 1,
				arrows: false,
				autoplay: true,
				autoplaySpeed: 5000
			}
		}
	}
}
</script>
<style lang="sass">
	.b-phone-slider
		position: relative
		width: 295px
		margin: 0 auto
		&__slides
			position: absolute
			top: 13px
			left: 42px
			right: 0
			bottom: 0
			width: 199px
			height: 429px

		.slick-dots
			bottom: 4rem
			li
				width: auto
				height: auto
				margin: .3rem
				button
					width: 11px
					height: 11px
					background-color: $color-light-silver
					+border-radius(50%)

					&:before
						display: none

				&.slick-active
					button
						background-color: $color-accent
		@media screen and (min-width: $lg)
			margin: 0
</style>



