<template lang="pug">
		div(:class="[customBlock ? 'b-feature b-feature--' + customBlock : 'b-feature']")
			<app-icon :name="icon" :custom="['b-feature__figure', 'b-feature__figure--' + icon, 'b-feature__figure--' + custom]"/>
			span.b-feature__caption
				slot(name="caption")
			p.b-feature__text
				slot(name="text")
</template>
<script>
import AppIcon from '@/components/global/AppIcon'
export default {
	props: ['caption', 'text', 'icon', 'mclass', 'custom', 'customBlock'],
	components: {
		AppIcon
	}
}
</script>
<style lang="sass">
	.b-features
		padding: 0 1.5rem
		&--beetwen
			justify-content: space-between
		&--md
			max-width: 80rem
			margin: 0 auto
	.b-feature
		padding: 3rem
		color: #ffffff
		text-align: center
		&--mb
			margin-bottom: 2.5rem

		&__figure
			display: block
			margin-bottom: 1rem
			margin-left: auto
			margin-right: auto
			color: $color-dark
			&--color
				color: $color-accent

			&--debit-card,
			&--reliability
				font-size: 4.2rem
			&--online-shop
				font-size: 4.4rem
			&--without-pecr,
			&--clients
				font-size: 4.1rem
			&--debit-card,
			&--reliability,
			&--cash
				font-size: 4.2rem
			&--online-shop
				font-size: 4.4rem
			&--credit-card-sm,
			&--invoice-sm,
			&--user-b-sm,
			&--sand-timer-sm,
			&--coins-sm,
			&--click-sm
				font-size: 3rem

		&__caption
			display: inline-block
			font-size: 1.6rem
			font-weight: 800
			margin-bottom: .8rem
		&__text
			font-size: 1.3rem
			font-weight: 300
			span
				font-size: 1.6rem
				font-weight: 800
	@media screen and (min-width: $sm)
		.b-feature
			padding: .5rem
			text-align: left
			&__figure
				margin-left: 0
				margin-right: 0
</style>



