import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home/index'
import Business from '@/components/business/index'

Vue.use(Router)

export default new Router({
	mode: 'history',
	routes: [
		{
			path: '/',
			name: 'home',
			component: Home
		},
		{
			path: '/business',
			name: 'business',
			component: Business
		}
	]
})



