<template lang="pug">
	.b-online-payments
		.b-online-payments__heading
			h1.e-h1 <span class="e-h1__light e-h1__light--block"> {{ $t("Online")}} </span> {{ $t("Payments")}} <span class="e-h1__small e-h1__small--block e-h1__small--mt">{{ $t("become easier, more convenient and faster")}}</span>
		.b-online-payments__form
			<app-phone-form customRow="right-md">
				template(slot="button") {{ $t("Start now")}}
				template(slot="inform") {{ $t("Your contact details will be completely safe")}}
			</app-phone-form>
</template>
<script>
import AppPhoneForm from '@/components/blocks/AppPhoneForm'
export default {
	components: {
		AppPhoneForm
	}
}
</script>
<style lang="sass">
	.b-online-payments
		display: flex
		flex-direction: column
		align-items: flex-end
		margin-bottom: 4rem
		&__heading
			text-align: right
			margin-bottom: 4rem
			.e-h1
				line-height: 1
				margin-bottom: 0
		&__form
			margin-left: auto
			margin-right: auto
		@media screen and (min-width: $sm)
			&__form
				margin-left: 0
				margin-right: 0

		@media screen and (min-width: $xl)
			margin-bottom: 5.5rem
			&__heading
				margin-bottom: 6.6rem
</style>



