<template lang="pug">
	a(href="/", :class="[custom ? 'b-logo b-logo--' + custom : 'b-logo']")
		template(v-if="this.custom == 'dark'")
			img(src="@/assets/img/logo-dark.png" alt="Logo").b-logo__pictures
		template(v-else)
			img(src="@/assets/img/logo.png" alt="Logo").b-logo__pictures
		span.b-logo__title Moneyfge

</template>
<script>
export default {
	props: ['custom']
}
</script>
<style lang="sass">
	.b-logo
		display: flex
		align-items: center
		&__pictures
			padding-right: 1rem
			max-width: 100px
		&__title
			font-size: 2rem
			font-weight: 700
			text-transform: uppercase
			color: #ffffff
		&--dark
			.b-logo__title
				color: $color-dark
		@media screen and (min-width: $lg)
			&__pictures
				padding-right: 8%
				max-width: 45%
			&__title
				font-size: 1.8rem
		@media screen and (min-width: $xl)
			&__pictures
				padding-right: 12.1%
				max-width: 55%
			&__title
				font-size: 2rem
</style>



