<template lang="pug">
	.b-merchant-step
		.b-merchant-step__numb
			slot(name="numb")
		h4.b-merchant-step__caption
			slot(name="caption")
		p.b-merchant-step__text
			slot(name="text")
		.b-merchant-step__button
			a(href="#").button.button--md-2
				slot(name="button")
</template>
<script>
export default {
	props: ['numb', 'caption', 'text', 'button']
}
</script>
<style lang="sass">
	.b-merchant-step
		&__numb
			font-size: 2.5rem
			font-weight: 800
			color: $color-accent
			margin-bottom: 1.5rem
		&__caption
			font-weight: 900
			margin-bottom: 1rem
		&__text
			font-size: 1.3rem
			font-weight: 300
			margin-bottom: 3rem
		@media screen and (min-width: $lg)
			&__numb
				font-size: 4rem
				margin-bottom: 2rem
			&__text
				margin-bottom: 6rem
</style>



