<template lang="pug">
	.b-social
		a(href="#").b-social__item
			<app-icon name="fa-twitter" custom="b-social__icon" />
		a(href="#").b-social__item
			<app-icon name="fa-facebook" custom="b-social__icon" />
</template>
<script>
import AppIcon from '@/components/global/AppIcon'
export default {
	components: {
		AppIcon
	}
}
</script>
<style lang="sass">
	.b-social
		display: flex
		align-items: center
		&__icon
			font-size: 14px
			color: $color-dark
			+transition(all, .4s, ease)
		&__item
			display: flex
			align-items: center
			justify-content: center
			flex-shrink: 0
			width: 4.5rem
			height: 4.5rem
			margin-right: 1.5rem
			border: .1rem solid #e6ebf1
			+border-radius(50%)
			+transition(all, .4s, ease)
			&:last-child
				margin-right: 0
			&:hover,
			&:focus
				background-color: $color-accent
				border-color: $color-accent
				.b-social__icon
					color: #ffffff
</style>



