<template lang="pug">
	div
		<app-hero mclass="personal">
			template(slot="block")
				<app-hero-slider>
					template(slot="item")
						div
							<app-online-payments/>
							.container.container--sm
								.row
									.col-sm-4
										<app-feature icon="debit-card" custom="color">
											template(slot="caption") {{ $t("Card support")}}
										</app-feature>
									.col-sm-4
										<app-feature icon="online-shop" custom="color">
											template(slot="caption") {{ $t("Any electronic payments")}}
										</app-feature>
									.col-sm-4
										<app-feature icon="reliability" custom="color">
											template(slot="caption") {{ $t("Secure payment guarantee")}}
										</app-feature>
					template(slot="item")
						div
							<app-online-payments/>
				</app-hero-slider>
		</app-hero>
		section.o-main-section.o-main-section--pb-md
			.container
				.o-main-section__heading.o-main-section__heading--mb-md
					<app-heading-section :caption="$t('Instant online transfers')" customBlock="sm">
						template(slot="subCaption") {{ $t("Instant online transfers DESC")}}
					</app-heading-section caption>
			.container.container--md
				.row
					.col-lg-4.col-sm-6
						<app-feature-card :caption="$t('Transfers from card to card')" icon="card-to-card">
							template(slot="text") {{$t("Transfer of funds")}}
						</app-feature-card>
					.col-lg-4.col-sm-6
						<app-feature-card :caption="$t('Remittance')" icon="money-send">
							template(slot="text") {{$t("Remittance DESC")}}
						</app-feature-card>
					.col-lg-4.col-sm-6
						<app-feature-card :caption="$t('Transfer between wallets')" icon="wallet-to-wallet">
						template(slot="text") {{$t("Transfer between wallets DESC")}}
						</app-feature-card>
					.col-lg-4.col-sm-6
						<app-feature-card :caption="$t('Debit card issue')" icon="debit-card">
						template(slot="text") {{$t("Debit card issue DESC")}}
						</app-feature-card>
					.col-lg-4.col-sm-6
						<app-feature-card :caption="$t('Fundraising')" icon="invoice">
						template(slot="text") {{$t("Fundraising DESC")}}
						</app-feature-card>
					.col-lg-4.col-sm-6
						<app-feature-card :caption="$t('Payment for various services')" icon="service-pay">
						template(slot="text") {{$t("Payment for various services DESC")}}
						</app-feature-card>
		section.o-main-section.o-main-section--pt-md
			.container.container--md
				.row.justify-content-lg-between
					.col-lg-4
						<app-phone-slider/>
					.col-lg-8
						.row
							.col-lg-8
								<app-heading-section :caption="$t('How to use the service from your mobile?')" customBlock="text-left b-heading-section--mb-md"></app-heading-section caption>
						<app-steps custom="mb-md">
						template(slot="step")
							<app-step custom="active">
							template(slot="caption") {{$t("Registration")}}
							template(slot="text") {{$t("Registration Step DESC")}}
							</app-step>
							<app-step >
							template(slot="caption") {{$t("Login")}}
							template(slot="text") {{$t("Login Step DESC")}}
							</app-step>
							<app-step>
							template(slot="caption") {{$t("Data")}}
							template(slot="text") {{$t("Data Step Desc")}}
							</app-step>
						</app-steps>
						<app-progress-line/>
						<app-qr-code/>
		section(:style="{ backgroundImage: 'url(' + require(`@/assets/img/section-bg/item-1.jpg`) + ')' }").o-main-section.o-main-section--overlay.o-main-section--color-white
			.container.container--md
				.o-main-section__heading
					<app-heading-section :caption="$t('Dont waste time standing in line-pay online with MoneyFGE')"></app-heading-section caption>
				.row
					.col-lg-3.col-sm-6
						<app-feature-card-ov :caption="$t('Top up mobile')" icon="mobile-pay">
							template(slot="text") {{$t('mobile operator')}}
							template(slot="button") {{$t('Replenish')}}
						</app-feature-card-ov>
					.col-lg-3.col-sm-6
						<app-feature-card-ov :caption="$t('Buy air ticket')" icon="air-tickets-black">
							template(slot="text") {{$t('Air ticket')}}
							template(slot="button") {{$t('Buy a ticket')}}
						</app-feature-card-ov>
					.col-lg-3.col-sm-6
						<app-feature-card-ov :caption="$t('Pay for media service')" icon="media-services">
							template(slot="text") {{$t('media service')}}
							template(slot="button") {{$t('Pay')}}
						</app-feature-card-ov>
					.col-lg-3.col-sm-6
						<app-feature-card-ov :caption="$t('Replenish the Internet')" icon="www">
							template(slot="text") {{$t('Internet provider')}}
							template(slot="button") {{$t('Replenish')}}
						</app-feature-card-ov>
					.col-lg-3.col-sm-6
						<app-feature-card-ov :caption="$t('Pay for communal services')" icon="house">
							template(slot="text") {{$t('Fast, Simple, Reliable')}}
							template(slot="button") {{$t('Pay')}}
						</app-feature-card-ov>
					.col-lg-3.col-sm-6
						<app-feature-card-ov :caption="$t('To pay fines')" icon="security">
							template(slot="text") {{$t('Fast, Simple, Reliable')}}
							template(slot="button") {{$t('Pay')}}
						</app-feature-card-ov>
					.col-lg-3.col-sm-6
						<app-feature-card-ov :caption="$t('Top up wallet')" icon="coins">
							template(slot="text") {{$t('Wallets')}}
							template(slot="button") {{$t('Replenish')}}
						</app-feature-card-ov>
					.col-lg-3.col-sm-6
						<app-feature-card-ov :caption="$t('To pay taxes')" icon="tax">
							template(slot="text") {{$t('Wallets')}}
							template(slot="button") {{$t('Pay')}}
						</app-feature-card-ov>
		section.o-main-section
			.container
				.o-main-section__heading.o-main-section__heading--mb-lg
					<app-heading-section :caption="$t('Will experience several advantages with the map MONEYFGE')">
						template(slot="subCaption") {{$t('Will experience several advantages with the map MONEYFGE DESC')}}
					</app-heading-section caption>
				.row.justify-content-md-between
					.col-lg-5
						<app-credit-card/>
					.col-lg-7
						<app-heading-section :caption="$t('Cash card')" customBlock="text-left b-heading-section--mb-md" customCaption="sm">
						</app-heading-section caption>
						<app-steps custom="mb-lg">
						template(slot="step")
							<app-step custom="active" captionCustom="custom">
							template(slot="caption") {{$t('Zero proc')}}
							template(slot="text") {{$t('Zero proc step DESC')}}
							</app-step>
							<app-step captionCustom="custom">
							template(slot="caption") {{$t('Numb one')}} <span class="b-steps__caption-small">{{$t('Day')}}</span>
							template(slot="text") {{$t('Numb one step DESC')}}
							</app-step>
							<app-step captionCustom="custom-weight">
							template(slot="caption") {{$t('Design')}}
							template(slot="text") {{$t('Design step DESC')}}
							</app-step>
						</app-steps>
						<app-heading-section :caption="$t('Leave your phone number, register and get a MONEY FGE card already!')" customBlock="text-left b-heading-section--mb-md" customCaption="sm">
						</app-heading-section caption>
						<app-phone-form custom="dark" customButton="dark button--md" customInform="custom-style">
							template(slot="button") {{$t("Get a card")}}
							template(slot="inform") {{ $t("Your contact details will be completely safe")}}
						</app-phone-form>
</template>
<script>
import AppHero from '@/components/objects/AppHero'
import AppHeroSlider from '@/components/objects/AppHeroSlider'
import AppOnlinePayments from './blocks/AppOnlinePayments'
import AppFeature from '@/components/blocks/AppFeature'
import AppHeadingSection from '../blocks/AppHeadingSection'
import AppFeatureCard from '../blocks/AppFeatureCard'
import AppPhoneSlider from './blocks/AppPhoneSlider'
import AppSteps from './blocks/AppSteps'
import AppStep from './blocks/AppStep'
import AppProgressLine from './blocks/AppProgressLine'
import AppQrCode from './blocks/AppQrCode'
import AppFeatureCardOv from './blocks/AppFeatureCardOv'
import AppCreditCard from './blocks/AppCreditCard'
import AppPhoneForm from '@/components/blocks/AppPhoneForm'
export default {
	components: {
		AppHero,
		AppHeroSlider,
		AppOnlinePayments,
		AppFeature,
		AppHeadingSection,
		AppFeatureCard,
		AppPhoneSlider,
		AppSteps,
		AppStep,
		AppProgressLine,
		AppQrCode,
		AppFeatureCardOv,
		AppCreditCard,
		AppPhoneForm
	}
}
</script>
<style lang="sass">
</style>



