<template lang="pug">
	.b-feature-card
		<app-icon :name="icon" :custom="'b-feature-card__figure b-feature-card__figure--' + icon"/>
		.b-feature-card__leave-block
			span.b-feature-card__caption {{caption}}
			.b-feature-card__hidden-block
				p.b-feature-card__desc
					slot(name="text")
				.b-feature-card__button
					a(href="#").button.button--md
						slot(name="button") {{ $t("More")}}
</template>
<script>
import AppIcon from '../global/AppIcon'
export default {
	props: ['caption', 'text', 'icon', 'button'],
	components: {
		AppIcon
	}
}
</script>
<style lang="sass">
	.b-feature-card
		display: flex
		flex-direction: column
		text-align: center
		overflow: hidden
		padding: 3.5rem
		background-color: $color-light
		height: 31.5rem
		+border-radius(.3rem)
		margin: 1.5rem 0

		&__figure
			display: block
			color: $color-dark
			margin: 19% auto 2rem
			flex-shrink: 0
			+transition(all , .4s, linear)
			&--card-to-card,
			&--money-send
				font-size: 4.8rem
			&--debit-card
				font-size: 4.7rem
			&--wallet-to-wallet
				font-size: 5.5rem
			&--service-pay,
			&--invoice
				font-size: 4.4rem
		&__caption
			margin-top: auto
			margin-bottom: 3rem
			font-size: 1.6rem
			font-weight: 800
		&__desc
			font-size: 13px
			font-weight: 300
			margin-bottom: 3rem
			height: 56px
			overflow: hidden
		&__button
			text-align: center
		&__leave-block
			+transition(all , .4s, linear)
			+translate(0, 44%)

		&__hidden-block
			opacity: 0
			visibility: hidden
		&:hover,
		&:focus
			color: #ffffff
			background-color: $color-light-dark

			.b-feature-card__figure
				color: $color-accent
				margin-top: 3%

			.b-feature-card__leave-block
				+translate(0, 0)
			.b-feature-card__hidden-block
				opacity: 1
				visibility: visible
	@media screen and (min-width: $sm)
		.b-feature-card
			text-align: left
			&__figure
				margin-left: 0
				margin-right: 0
</style>



