<template lang="pug">
	div(:class="['b-steps', 'b-steps--' + custom]")
		slot(name="step")
</template>
<script>
export default {
	props: ['step', 'custom']
}
</script>
<style lang="sass">
	.b-steps
		display: flex
		flex-direction: column
		justify-content: center
		align-items: center
		&--mb-md
			margin-bottom: 4rem
		&--mb-lg
			margin-bottom: 7.5rem

		@media screen and (min-width: $sm)
			flex-direction: row
			justify-content: flex-start
</style>



