<template lang="pug">
	.b-secure
		template(v-for="item in items")
			a(:href="item.src").b-secure__item
				<app-spr :name="item.icon"/>
</template>
<script>
import AppSpr from '@/components/blocks/AppSpr'
export default {
	components: {
		AppSpr
	},
	data () {
		return {
			items: [
				{ icon: 'vvisa', src: '#' },
				{ icon: 'mc', src: '#' },
				{ icon: 'pcidss', src: '#' },
				{ icon: 'ssl', src: '#' },
				{ icon: 'mcafee', src: '#' }
			]
		}
	}
}
</script>
<style lang="sass">
	.b-secure
		display: flex
		align-items: center
		flex-wrap: wrap
		justify-content: center
		&__item
			flex-shrink: 0
			padding-right: 1.8rem

			&:last-child
				padding-right: 0
</style>



