<template lang="pug">
	div(:class="[active ? 'b-successfully ' + active : 'b-successfully']")
		.b-successfully__body
			.b-successfully__caption
				slot(name="caption")
			.b-successfully__text
				slot(name="text")
</template>
<script>
export default {
	props: ['caption', 'text', 'active']
}
</script>
<style lang="sass">
	.b-successfully
		position: fixed
		top: 20%
		left: -100%
		background-color: $color-accent
		border-bottom-right-radius: 3em
		border-top-right-radius: 3em
		padding: 1rem 2rem
		z-index: 5
		overflow: hidden
		&__text
			font-size: 1.3rem
			font-weight: 300
		&__body
			opacity: 0
		&.active
			animation: revealRTL 7s ease
			.b-successfully__body
				animation: textHidden 5s 1s  forwards

		@keyframes revealRTL
			0%
				left: -100%

			25%
				left: 0
			75%
				left: 0
			100%
				left: -100%
		@keyframes textHidden
			0%
				opacity: 0
			25%
				opacity: 1
			75%
				opacity: 1
			100%
				opacity: 0
</style>



