<template lang="pug">
	.b-feature-card-overlay
		.b-feature-card-overlay__body
			.b-feature-card-overlay__figure-wrap
				<app-icon :name="icon" :custom="'b-feature-card-overlay__figure b-feature-card-overlay__figure--' + icon"/>
			span.b-feature-card-overlay__caption {{caption}}
			p.b-feature-card-overlay__desc
				slot(name="text")
			.b-feature-card-overlay__hidden
				.b-feature-card-overlay__button
					a(href="#").button
						slot(name="button") {{ $t("Replenish")}}
</template>
<script>
import AppIcon from '@/components/global/AppIcon'
export default {
	props: ['caption', 'text', 'icon', 'button'],
	components: {
		AppIcon
	}
}
</script>
<style lang="sass">
	.b-feature-card-overlay
		position: relative
		height: 100%
		text-align: center
		//padding: 2rem
		&__figure-wrap
			margin-bottom: 1.6rem
			height: 4.7rem
		&__figure
			color: $color-accent
			margin-left: auto
			margin-right: auto
			&--mobile-pay,
			&--media-services
				font-size: 4.5rem
			&--air-tickets-black
				font-size: 3.8rem
				+rotate(-90deg)
			&--www
				font-size: 3.9rem
			&--house
				font-size: 4.4rem
			&--security
				font-size: 4.7rem
			&--coins
				font-size: 4.3rem
			&--tax
				font-size: 4.2rem

		&__caption
			display: block
			font-size: 1.6rem
			font-weight: 800
			margin-bottom: 1.4rem

		&__desc
			font-size: 1.3rem
			font-weight: 300
			margin-bottom: 0
			height: 36px
			overflow: hidden

		&__button
			text-align: center

		&__body
			position: relative
			padding: 2rem
			background-color: rgba($color-light-dark, 0)
			+transition(all, .4s, ease)
		&__hidden
			opacity: 0
			visibility: hidden
			position: absolute
			left: 0
			right: 0
			top: 99%
			background-color: $color-light-dark
			//padding-top: 4rem
			padding-bottom: 2rem
			z-index: 1
			+transition(all, .4s, ease)
		&:hover,
		&:focus
			.b-feature-card-overlay__body
				background-color: rgba($color-light-dark, 1)
			.b-feature-card-overlay__hidden
				opacity: 1
				visibility: visible
		@media screen and (min-width: $sm)
			text-align: left
			&__figure
				margin-left: 0
</style>



