<template lang="pug">
	div
		<app-hero mclass="business">
			template(slot="block")
				<app-hero-slider custom="business">
					template(slot="item")
						div
							<app-online-payments/>
								.row
									.col-sm-4
										<app-feature icon="without-pecr" custom="color">
											template(slot="caption") {{ $t("No Commission")}}
											template(slot="text") <span>{{ $t("2.5 proc")}}</span> - {{ $t("Only for accepting payments")}}
										</app-feature>
									.col-sm-4
										<app-feature icon="cash" custom="color">
											template(slot="caption") {{ $t("Quick withdrawals")}}
											template(slot="text") <span> {{$t("for 2 days")}} </span> - {{$t("Payments to Bank account")}}
										</app-feature>
									.col-sm-4
										<app-feature icon="clients" custom="color">
											template(slot="caption") {{ $t("New client")}}
											template(slot="text") <span>{{ $t("from day 1")}}  </span> - {{ $t("Online shopping will attract new customers")}}
										</app-feature>
						div
							<app-online-payments/>
							.row
								.col-sm-4
									<app-feature icon="without-pecr" custom="color">
									template(slot="caption") {{ $t("No Commission")}}
									template(slot="text") <span>{{ $t("2.5 proc")}}</span> - {{ $t("Only for accepting payments")}}
									</app-feature>
								.col-sm-4
									<app-feature icon="cash" custom="color">
									template(slot="caption") {{ $t("Quick withdrawals")}}
									template(slot="text") <span> {{$t("for 2 days")}} </span> - {{$t("Payments to Bank account")}}
									</app-feature>
								.col-sm-4
									<app-feature icon="clients" custom="color">
									template(slot="caption") {{ $t("New client")}}
									template(slot="text") <span>{{ $t("from day 1")}}  </span> - {{ $t("Online shopping will attract new customers")}}
									</app-feature>
				</app-hero-slider>
		</app-hero>
		section.o-main-section.o-main-section--overflow.o-main-section--merchant
			.container-fluid
				.o-main-section__heading.o-main-section__heading--mb-0
					<app-heading-section :caption="$t('How does merchant work?')" customCaption="lg b-heading-section__caption--semi">
					</app-heading-section caption>
			.container.container--md
				<app-merchant-slider/>
		section(:style="{ backgroundImage: 'url(' + require(`@/assets/img/business/section-calc.jpg`) + ')' }").o-main-section.o-main-section--overlay
			.container.container--lg
				.row.no-gutters
					.col.col-lg-6
						<app-com-calc/>
					.col-lg-6
						<app-for-business/>
		section.o-main-section
			.container
				.o-main-section__heading.o-main-section__heading--mb-md
					<app-heading-section :caption="$t('Send a connection request')" customCaption="lg b-heading-section__caption--semi">
						template(slot="subCaption") {{$t("Your request will be reviewed within 24 hours")}}
					</app-heading-section caption>
				.row.justify-content-center
					.col-lg-8.col-xl-7
						<app-connect-form/>
</template>
<script>
import AppHero from '@/components/objects/AppHero'
import AppHeroSlider from '@/components/objects/AppHeroSlider'
import AppOnlinePayments from './blocks/AppOnlinePayments'
import AppFeature from '@/components/blocks/AppFeature'
import AppMerchantSlider from './blocks/AppMerchantSlider'
import AppHeadingSection from '@/components/blocks/AppHeadingSection'
import AppComCalc from './blocks/AppComCalc'
import AppForBusiness from './blocks/AppForBusiness'
import AppConnectForm from './blocks/AppConnectForm'
export default {
	components: {
		AppHero,
		AppHeroSlider,
		AppOnlinePayments,
		AppFeature,
		AppMerchantSlider,
		AppHeadingSection,
		AppComCalc,
		AppForBusiness,
		AppConnectForm
	}
}

</script>



