<template lang="pug">
	form(v-on:submit.prevent="formSubmit", :class="[custom ? 'b-phone-form b-phone-form--' + custom : 'b-phone-form']")
		div(:class="[customRow ? 'b-phone-form__row b-phone-form__row--' + customRow : 'b-phone-form__row']")
			.b-phone-form__input-wrap
				<app-phone-input :setPhone='setPhone'/>
			.b-phone-form__button
				button(:class="[customButton ? 'button button--' + customButton : 'button button--md']")
					slot(name="button")
		span(:class="[customInform ? 'b-phone-form__information-text b-phone-form__information-text--' + customInform : 'b-phone-form__information-text']")
			slot(name="inform")
</template>
<script>
import AppPhoneInput from '@/components/blocks/AppPhoneInput'
export default {
	props: ['custom', 'button', 'inform', 'customRow', 'customInform', 'customButton'],
	components: {
		AppPhoneInput
	},
	data () {
		return {
			phone: '',
			valid: false
		}
	},
	methods: {
		setPhone (data) {
			this.phone = data.number
			this.valid = data.valid

			if (this.valid) {
				this.$el.classList.remove('error')
			}
		},
		formSubmit () {
			if (this.valid) {
				window.location.href = 'auth/registration'
				localStorage.setItem('MoneyFGEPhone', this.phone)
			} else {
				this.$el.classList.add('error')
			}
		}
	}
}
</script>
<style lang="sass">
	.b-phone-form
		max-width: 448px
		margin-left: auto
		margin-right: auto
		&__row
			display: flex
			align-items: center
			flex-direction: column
		&__input-wrap
			position: relative
			padding-right: 0
			margin-bottom: 1.5rem

		&__flag
			position: absolute
			top: 50%
			left: 0
			+translate(0, -50%)

		&__input-phone
			padding: 1rem 1rem .9rem 5.7rem
			&--light
				color: #ffffff
				border-bottom-color: rgba(255,255,255, .3)
		&__button
			text-align: right
		&__information-text
			display: block
			font-size: 1.3rem
			font-weight: 300
			margin-top: 2rem
			text-align: center
			margin-left: auto
			margin-right: auto
			max-width: 30rem
			&--custom-style
				margin-top: 1rem
		&--dark
			.b-phone-form__input-phone
				border-bottom-color: rgba(#101010, .3)
		@media screen and (min-width: $sm)
			&__row
				flex-direction: row
				&--right-md
					justify-content: flex-end
			&__input-wrap
				padding-right: 2.8%
			&__information-text
				margin-left: 0
				margin-right: 0
				max-width: 100%
				text-align: right
				&--custom-style
					text-align: center
		@media screen and (min-width: $lg)
			max-width: 540px
			margin-left: 0
			margin-right: 0
			&__information-text
				&--custom-style
					text-align: left
					max-width: 60%
		@media screen and (min-width: $xl)
			&__button
				flex: 1 0 40%
				max-width: 40%
</style>



