<template lang="pug">
	.b-for-business
		h3.b-for-business__caption {{ $t("Business solutions")}}
		.row.justify-content-center
			.col-lg-5.col-sm-6
				<app-feature icon="credit-card" custom="credit-card-sm b-feature__figure--color" customBlock="mb">
					template(slot="caption") {{ $t("Credit card payment")}}
				</app-feature>
			.col-lg-5.col-sm-6
				<app-feature icon="invoice" custom="invoice-sm b-feature__figure--color" customBlock="mb">
					template(slot="caption") {{ $t("Fast invoicing")}}
				</app-feature>
			.col-lg-5.col-sm-6
				<app-feature icon="user-b" custom="user-b-sm b-feature__figure--color" customBlock="mb">
					template(slot="caption") {{ $t("Personal business Cabinet")}}
				</app-feature>
			.col-lg-5.col-sm-6
				<app-feature icon="sand-timer" custom="sand-timer-sm b-feature__figure--color" customBlock="mb">
					template(slot="caption") {{ $t("Auto payments")}}
				</app-feature>
			.col-lg-5.col-sm-6
				<app-feature icon="coins" custom="coins-sm b-feature__figure--color" customBlock="mb">
					template(slot="caption") {{ $t("Mass payments")}}
				</app-feature>
			.col-lg-5.col-sm-6
				<app-feature icon="click" custom="click-sm b-feature__figure--color" customBlock="mb">
					template(slot="caption") {{ $t("Payments in 1 click")}}
				</app-feature>
</template>
<script>
import AppFeature from '@/components/blocks/AppFeature'
export default {
	components: {
		AppFeature
	}
}
</script>
<style lang="sass">
	.b-for-business
		position: relative
		padding: 4.5rem 4rem 5.5rem 4rem
		&__caption
			font-size: 3rem
			line-height: 1.1
			font-weight: 800
			letter-spacing: 0
			margin-bottom: 4rem
			color: #ffffff

		@media screen and (min-width: $lg)
			&__caption
				font-size: 3.6rem
		@media screen and (min-width: $xl)
			padding: 4.5rem 5.7rem 4.5rem 7.7rem
			&__caption
				font-size: 4.5rem
				margin-bottom: 6.5rem
</style>



