<template lang="pug">
	footer.c-footer
		.container
			.row.align-items-center.justify-content-between.c-footer__row
				.col-lg-3.col-sm-6
					.c-footer__logo
						<app-logo custom="dark"/>
				.col-lg-2.col-sm-6
					.c-footer__change-template
						<app-change-template custom="dark"/>
				<!--.col-lg-5-->
					<!--.c-footer__subscribe-form-->
						<!--<app-subscribe/>-->
			.row.justify-content-between
				.col-lg-3.order-1.order-lg-0
					span.c-footer__copiryght {{ $t("All rights reserved")}}
					.c-footer__social
						<app-social/>
				.col-lg-2.col-sm-4
					.c-footer__nav
						<app-main-nav-list/>
				.col-lg-5.col-sm-8
					.c-footer__technical
						.c-footer__mail
							a(href="mailto:info@moneyfge.com").e-color-link info@moneyfge.com
						.c-footer__secure
							<app-secure/>

</template>
<script>

import AppLogo from '@/components/blocks/AppLogo'
import AppChangeTemplate from '@/components/blocks/AppChangeTemplate'
import AppSubscribe from '@/components/blocks/AppSubscribe'
import AppSocial from '@/components/blocks/AppSocial'
import AppMainNavList from '@/components/blocks/AppMainNavList'
import AppSecure from '@/components/blocks/AppSecure'
export default {
	components: {
		AppLogo,
		AppChangeTemplate,
		AppSubscribe,
		AppSocial,
		AppMainNavList,
		AppSecure
	}
}
</script>



