<template lang="pug">
	.b-merchant-slider
		.b-merchant-slider__navigation.js-merchant-navigation
			.b-slider-navigation.b-slider-navigation--vertical
				button(@click="prev").js-slick-prev.b-slider-navigation__arrow.b-slider-navigation__arrow--prev
					<app-icon name="up" custom="b-slider-navigation__arrow-icon" />
				.js-slick-dots.b-slider-navigation__dots#mainSlider__dots1
				button(@click="next").js-slick-next.b-slider-navigation__arrow.b-slider-navigation__arrow--next
					<app-icon name="up" custom="b-slider-navigation__arrow-icon" />
		.row.align-items-center.justify-content-lg-end.no-gutters
			.col-xl-4.col-lg-3
				.b-merchant-slider__slider.js-merchant-slider
					slick(ref="slick" :options="slickOptions")
						.b-merchant-slider__item
							<app-merchant-step>
								template(slot="numb") {{$t("Stage")}} 1
								template(slot="caption") {{$t("Product selection")}}
								template(slot="text") {{$t("The buyer comes to your website and chooses a product or service")}}
								template(slot="button") {{$t("Demo store")}}
							</app-merchant-step>
						.b-merchant-slider__item
							<app-merchant-step>
							template(slot="numb") {{$t("Stage")}} 2
							template(slot="caption") {{$t("Product selection")}}
							template(slot="text") {{$t("The buyer comes to your website and chooses a product or service")}}
							template(slot="button") {{$t("Demo store")}}
							</app-merchant-step>
			.col-xl-7.col-lg-8
				.b-merchant-slider__ipad
					<app-ipad-block/>
					.b-merchant-slider__ipad-screen
						<app-ipad-screen/>
					.b-merchant-slider__ipad-accessories
						<app-ipad-acces/>
</template>
<script>
import Slick from 'vue-slick'
import AppIcon from '@/components/global/AppIcon'
import AppSliderNav from '@/components/blocks/AppSliderNav'
import AppMerchantStep from './AppMerchantStep'
import AppIpadBlock from './AppIpadBlock'
import AppIpadScreen from './AppIpadScreen'
import AppIpadAcces from './AppIpadAcces'
export default {
	components: {
		Slick,
		AppIcon,
		AppSliderNav,
		AppMerchantStep,
		AppIpadBlock,
		AppIpadScreen,
		AppIpadAcces
	},
	data () {
		return {
			slickOptions: {
				dots: true,
				arrows: true,
				vertical: true,
				verticalSwiping: true,
				slidesToShow: 1,
				slidesToScroll: 1,
				appendDots: '.js-slick-dots',
				asNavFor: '.js-merchant-ipad-slider'
			}
		}
	},
	methods: {
		next () {
			this.$refs.slick.next()
		},

		prev () {
			this.$refs.slick.prev()
		}
	}
}
</script>
<style lang="sass">
	.b-merchant-slider
		position: relative
		padding-left: 5rem
		&__navigation
			position: absolute
			top: 45%
			left: 0
			z-index: 1
			+translate(0, -50%)
		&__ipad
			position: relative
			display: none
		&__ipad-screen
			position: absolute
			top: 66px
			left: 85px
			z-index: 1
			width: 414px
			height: 310px

		&__slider
			margin-top: 7rem
		&__ipad-accessories
			position: absolute
			top: 0
			right: 0
			bottom: 0
			left: 0
			z-index: 1
		@media screen and (min-width: $lg)
			padding-left: 0
			&__navigation
				left: -3rem
			&__item
			&__ipad
				display: block
	.b-slider-navigation
		display: flex
		align-items: center
		justify-content: center
		&__dots
			margin: 0 2.5rem
			.slick-dots
				position: relative
				top: 0
				left: 0
				display: flex
				align-items: center
				justify-content: center

				li
					display: flex
					align-items: center
					justify-content: center
					cursor: pointer
					width: 2.5rem
					height: 2.5rem
					border: 1px solid transparent
					background: transparent
					margin: .5rem
					+border-radius(50%)
					button
						display: block
						width: .5rem
						height: .5rem
						background-color: $color-dark
						padding: 0
						+border-radius(50%)
						&:before
							display: none

					&.slick-active
						border-color: $color-accent
						button
							width: .9rem
							height: .9rem
							background-color: $color-accent
		&__arrow
			position: relative
			display: inline-flex
			align-items: center
			justify-content: center
			width: 2.5rem
			height: 2.5rem
			&--next
				.b-slider-navigation__arrow-icon
					+rotate(-180deg)
		&__arrow-icon
			font-size: 1.3rem
			color: $color-accent

		&--vertical
			flex-direction: column
			.b-slider-navigation
				&__dots
					margin: 2.5rem 0
					.slick-dots
						flex-direction: column
</style>



