<template lang="pug">
	i(:class="['e-spr-logo', 'e-spr-logo--' + name]")
</template>
<script>
export default {
	props: ['name']
}
</script>
<style lang="sass">
	.e-spr-logo
		position: relative
		display: inline-block
		background: transparent url('~@/assets/img/sprite-png/logo.png') center no-repeat
		max-width: 100%
		&--beeline
			width: 524px
			height: 160px
			background-position: -10px -10px
		&--kyivstar
			width: 200px
			height: 74px
			background-position: -230px -320px
		&--lifecell
			width: 200px
			height: 80px
			background-position: -270px -190px
		&--mc
			width: 65px
			height: 24px
			background-position: -450px -320px
		&--mcafee
			width: 65px
			height: 23px
			background-position: -117px -417px
		&--mts
			width: 87px
			height: 31px
			background-position: -10px -417px
		&--pcidss
			width: 57px
			height: 23px
			background-position: -202px -417px
		&--s7
			width: 240px
			height: 110px
			background-position: -10px -190px
		&--ssl
			width: 43px
			height: 20px
			background-position: -490px -190px
		&--tele2
			width: 63px
			height: 25px
			background-position: -450px -364px
		&--uia
			width: 200px
			height: 77px
			background-position: -10px -320px
		&--vvisa
			width: 54px
			height: 24px
			background-position: -10px -468px
</style>



