<template lang="pug">
	form(v-on:submit="sendForm").b-main-form
		template(v-if="formSuccess")
			<app-successfully :active="formSuccess ? 'active' : ''">
				template(slot="caption") {{$t('Congratulations')}}
				template(slot="text") {{$t('Your request has been successfully sent')}}
			</app-successfully>
		.b-main-form__input-row
			.col-md-8
				.b-main-form__input-block
					.b-main-form__input-focus
						input(type="text", :placeholder="$t('Your name')", v-model="fullName" name="name" required).b-main-form__input
			.col-md-4
				.b-main-form__input-block
					.b-main-form__hidden-input
						input(type="text"  v-model="country" required)
					<v-select :placeholder="$t('Chosen country')" :options="countryList" v-model="country"></v-select>
					<!--select(v-model="selectedCoutry")-->
						<!--option(disabled selected value="") {{ $t('Chosen country') }}-->
						<!--option(v-for="country in countryList") {{ country.name }}-->
		.b-main-form__input-row
			.col-md-8
				.b-main-form__input-block
					.b-main-form__input-focus
						input(type="text", :placeholder="$t('Your name')", value="" v-model="email" required).b-main-form__input
			.col-md-4
				.b-main-form__input-block
					.b-main-form__hidden-input
						input(type="text"  v-model="companyType" required)
					<v-select :placeholder="$t('Type of organization')" :options="organizationList" v-model="companyType"></v-select>
		.b-main-form__input-row
			.col-md-8
				.b-main-form__input-block
					.b-main-form__input-focus
						input(type="text", :placeholder="$t('Company name')", value="" v-model="companyName"  required).b-main-form__input
			.col-md-4
				.b-main-form__input-block
					.b-main-form__input-focus
						input(type="text", :placeholder="$t('Web site company')", value="" v-model="siteUrl").b-main-form__input

		.b-main-form__input-row
			.col
				.b-main-form__textarea-block
					textarea(placeholder="Расскажите вкратце о Вашей компаии" v-model="aboutCompany").b-main-form__textarea
		.b-main-form__input-row
			.col
				span.b-main-form__request {{ $t("Your contact details will be completely safe 2")}}
		.b-main-form__button
			.col
				button.button.button--lg {{ $t("Send a request")}}
</template>
<script>
import AppInput from '@/components/blocks/AppInput'
import AppSuccessfully from '@/components/global/AppSuccessfully'
export default {
	components: {
		AppInput,
		AppSuccessfully
	},
	data () {
		return {
			fullName: '',
			countryList: [],
			selectedCoutry: '',
			country: '',
			email: '',
			organizationList: [
				{id: 1, label: 'Страна 1'},
				{id: 3, label: 'Страна 2'},
				{id: 2, label: 'Страна 3'}
			],
			companyType: '',
			companyName: '',
			siteUrl: '',
			aboutCompany: '',
			formSuccess: false
		}
	},
	methods: {
		getCountriesList () {
			this.$http.get('https://restcountries.eu/rest/v2/all').then(function (response) {
				let country = response.body
				let list = []
				country.forEach(function (item, i) {
					list.push({
						id: i,
						label: item.name
					})
				})
				this.countryList = list
			})
		},
		sendForm: function (event) {
			event.preventDefault()
			if (this.fullName && this.country && this.email && this.companyName) {
				sendForm(this.fullName, this.country, this.email, this.siteUrl, this.companyName, this.companyType, this.aboutCompany)
				this.formSuccess = true
				event.target.reset()
				setTimeout(function () {
					this.formSuccess = false
				}, 7500)
			} else {
				return false
			}
		}
	},
	created: function () {
		this.getCountriesList()
	}
}
function sendForm (fullName, country, email, siteUrl, companyName, companyType, aboutCompany) {
	var payload = JSON.stringify({
		fullName: fullName,
		country: country,
		email: email,
		siteUrl: siteUrl,
		companyName: companyName,
		companyType: companyType,
		aboutCompany: aboutCompany
	})

	sendApiReq('\g' + CONFIG_API_ENDPOINT + 'merchant/bid', 'POST', payload).then(
		function (resp) {
			if (resp.id) {
				console.log(true)
			} else {
				console.log(false)
			}
		},
		function (err) {
			console.log(false)
		}
	)
}
</script>



