<template lang="pug">
	section(:class="[mclass ?  'o-hero o-hero--' + mclass : 'o-hero']")
		.container
			.row
				.col
					slot(name="block")
</template>
<script>
export default {
	props: ['block', 'mclass', 'bgi']
}
</script>
<style lang="sass">
	.o-hero
		display: flex
		align-items: center
		position: relative
		background: transparent center no-repeat
		background-attachment: fixed
		background-size: cover

		min-height: 100vh
		color: #ffffff

		padding: 13rem 0 3rem
		&:before
			content: ''
			position: absolute
			top: 0
			right: 0
			bottom: 0
			left: 0
			background-image: radial-gradient(circle 999px at center, rgba(11, 7, 2, 0.2) 0%, rgba(9, 11, 20, 0.75) 100%)
		&--personal
			background-image: url('~@/assets/img/personal/hero.jpg')
		&--business
			background-image: url('~@/assets/img/business/hero.jpg')
			&:before
				background-image: radial-gradient(circle 963px at center, rgba(39, 34, 27, 0.5) 0%, #38404f 100%)
		@media screen and (min-width: $lg)
			padding: 17rem 0 5rem
</style>



