<template lang="pug">
	div(:class="[customBlock ? 'b-heading-section b-heading-section--' + customBlock : 'b-heading-section']")
		h2(:class="[customCaption ? 'b-heading-section__caption b-heading-section__caption--' + customCaption : 'b-heading-section__caption']") {{caption}}
		template()
			p(:class="[customSub ? 'b-heading-section__sub-caption b-heading-section__sub-caption--' + customSub : 'b-heading-section__sub-caption']")
				slot(name="subCaption")
</template>
<script>
export default {
	props: ['caption', 'subCaption', 'customBlock', 'customCaption', 'customSub']
}

</script>
<style lang="sass">
	.b-heading-section
		text-align: center
		&--sm
			max-width: 53rem
			margin-left: auto
			margin-right: auto

		&--mb-md
			margin-bottom: 5rem

		&__caption
			font-size: 2.6rem
			line-height: 1.1
			font-weight: 800
			margin-bottom: 0
			&--sm
				font-size: 1.8rem
			&--semi
				font-weight: 500
		&__sub-caption
			font-size: 1.3rem
			font-weight: 300
			margin-top: 1rem

		@media screen and (min-width: $lg)
			&--text-left
				text-align: left

			&__caption
				font-size: 3.2rem
				&--lg
					font-size: 3.6rem
				&--sm
					font-size: 1.8rem
		@media screen and (min-width: $xl)
			&__caption
				font-size: 3.6rem
				&--lg
					font-size: 4.5rem
				&--sm
					font-size: 2.2rem
</style>



