<template lang="pug">
	div(:class="[custom ? 'b-change-template b-change-template--' + custom : 'b-change-template']")
		router-link(:to="{name: 'home'}").b-change-template__item {{ $t("Personal")}}
		router-link(:to="{name: 'business'}").b-change-template__item {{ $t("Business")}}

</template>

<script>
export default {
	props: ['custom']
}
</script>
<style lang="sass">
	.b-change-template
		position: relative
		display: flex
		align-items: center
		&__item
			font-size: 14px
			font-weight: 700
			color: #ffffff
			text-transform: uppercase
			letter-spacing: 0.07rem
			+transition(all, .4s, ease)
			margin-right: 2rem
			padding: .6rem 0 .4rem
			&:last-child
				margin-right: 0 !important
		&--dark
			.b-change-template__item
				color: $color-dark
		&__item
			&.router-link-exact-active
				color: $color-accent
				border-bottom: 2px solid $color-accent

		@media screen and (min-width: $lg)
			&__item
				margin-right: 17%
</style>



