<template lang="pug">
	.b-slider-navigation.b-slider-navigation--vertical
		button(@click="prev").js-slick-prev.b-slider-navigation__arrow.b-slider-navigation__arrow--prev
			<app-icon name="up" custom="b-slider-navigation__arrow-icon" />
		.js-slick-dots.b-slider-navigation__dots
		button(@click="next").js-slick-next.b-slider-navigation__arrow.b-slider-navigation__arrow--next
			<app-icon name="up" custom="b-slider-navigation__arrow-icon" />
</template>
<script>
import Slick from 'vue-slick'
import AppIcon from '@/components/global/AppIcon'
export default {
	components: {
		AppIcon,
		Slick
	},
	methods: {
		next () {
			this.$refs.slick.next()
		},

		prev () {
			this.$refs.slick.prev()
		}
	}
}
</script>
<style lang="sass">
	.b-slider-navigation
		display: flex
		align-items: center
		justify-content: center
		&__dots
			margin: 0 2.5rem
			.slick-dots
				position: relative
				top: 0
				left: 0
				display: flex
				align-items: center
				justify-content: center

				li
					display: flex
					align-items: center
					justify-content: center
					cursor: pointer
					width: 2.5rem
					height: 2.5rem
					border: 1px solid transparent
					background: transparent
					margin: .5rem
					+border-radius(50%)
					button
						display: block
						width: .5rem
						height: .5rem
						background-color: $color-dark
						padding: 0
						+border-radius(50%)
						&:before
							display: none

					&.slick-active
						border-color: $color-accent
						button
							width: .9rem
							height: .9rem
							background-color: $color-accent
		&__arrow
			position: relative
			display: inline-flex
			align-items: center
			justify-content: center
			width: 2.5rem
			height: 2.5rem
			&--next
				.b-slider-navigation__arrow-icon
					+rotate(-180deg)
		&__arrow-icon
			font-size: 1.3rem
			color: $color-accent

		&--vertical
			flex-direction: column
			.b-slider-navigation
				&__dots
					margin: 2.5rem 0
					.slick-dots
						flex-direction: column

</style>



