<template lang="pug">
	div
		.b-menu
			button.b-menu__button(@click="toggleMobileMenu")
				.b-menu__burger
					.burger
						.burger__icon
				span.b-menu__text {{ $t("Menu")}}
		.b-mobile-menu(v-if="showMobileMenu")
			.b-mobile-menu__body.container
				.b-mobile-menu__top-line
					.b-mobile-menu__change
						<app-change-template/>
					.b-mobile-menu__lang
						<app-Language/>
				.b-mobile-menu__navigation
					<app-navigation/>
				.b-mobile-menu__authorization
					.b-header-add-features.b-header-add-features--center
						.b-header-add-features__item
							<app-auth/>
</template>
<script>
import AppChangeTemplate from './AppChangeTemplate'
import AppLanguage from './AppLanguage'
import AppAuth from './AppAuth'
import AppNavigation from './AppNavigation'

export default {
	components: {
		AppChangeTemplate,
		AppLanguage,
		AppAuth,
		AppNavigation
	},
	data () {
		return {
			showMobileMenu: false
		}
	},
	methods: {
		toggleMobileMenu () {
			this.showMobileMenu = !this.showMobileMenu
			document.querySelector('.c-page').classList.toggle('js-modal-open')
		}
	}
}
</script>
<style lang="sass">
	.b-menu
		&__button
			display: flex
			align-items: center
			margin-left: auto
		&__burger
			margin-right: 0
		&__text
			position: relative
			z-index: 1
			display: none
			font-size: 13px
			color: #ffffff
			letter-spacing: 0.065em
			text-transform: uppercase

		@media screen and (min-width: $lg)
			&__button
				margin-left: 0
			&__burger
				margin-right: 17%
			&__text
				display: inline-block

	.burger
		position: relative
		width: 52px
		height: 52px
		transition-duration: 0.5s
		z-index: 101
		border: 1px solid #ffffff
		+border-radius(50%)

		&:hover
			cursor: pointer
		&__icon

			position: absolute
			top: 50%
			left: 50%
			height: 3px
			width: 21px
			transition-duration: 0.5s
			background-color: $color-accent
			+translate(-50%, -50%)

			&:before
				transition-duration: 0.5s
				position: absolute
				top: -6px
				left: 0
				width: inherit
				height: inherit
				background-color: inherit
				content: ""

			&:after
				transition-duration: 0.5s
				position: absolute
				top: 6px
				left: 0
				width: .8em
				height: inherit
				background-color: inherit
				content: ""

		&.active
			.burger__icon
				transition-duration: 0.5s
				background: transparent
				&:before
					-webkit-transform: rotateZ(45deg) scaleX(1.25) translate(4px, 4px)
					transform: rotateZ(45deg) scaleX(1.25) translate(4px, 4px)
					background-color: $color-accent
				&:after
					-webkit-transform: rotateZ(-45deg) scaleX(1.25) translate(4px, -4px)
					transform: rotateZ(-45deg) scaleX(1.25) translate(4px, -4px)
					background-color: $color-accent
					width: inherit

		@media screen and (min-width: $lg)
			width: 45px
			height: 45px
			&__icon
				width: 18px
		@media screen and (min-width: $xl)
			width: 52px
			height: 52px
			&__icon
				width: 21px
	.b-mobile-menu
		position: fixed
		top: 0
		right: 0
		bottom: 0
		left: 0
		overflow-x: auto
		background-color: rgba($color-dark, .8)
		//opacity: 0
		//visibility: hidden
		//transform: scale(.5) translate(0, -100%)
		+transition(all, .5s, linear)
		&.active
			//opacity: 1
			//visibility: visible
			//transform: scale(1) translate(0, 0)
		&__body
			display: flex
			flex-direction: column
			padding: 10rem 15px 5rem
			min-height: 100%
		&__top-line
			display: flex
		&__authorization,
		&__navigation
			margin-top: auto
		&__change,
		&__lang
			margin-left: auto
		&__lang
			display: flex
			justify-content: flex-end
			margin-bottom: 1rem

		.b-header-add-features
			display: flex
			align-items: center
			justify-content: flex-end
			&--center
				justify-content: center
			&__item
				padding-right: 2rem
				&:last-child
					padding-right: 0

	@media screen and (min-width: $lg)
		.b-mobile-menu
			&__authorization,
			&__change,
			&__lang
				display: none
			&__navigation
				margin: auto
		.b-header-add-features
			&__item
				padding-right: 3.5rem
</style>



