<template lang="pug">
	.b-language
		.b-language__header(@click="toggleDropdown", :class="{ active: isActive }")
			i(:class="['e-lang-icon', 'e-lang-icon--' + language]")
			.b-language__header-icon-sort
				<svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="sort-down" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512" class="svg-inline--fa fa-sort-down fa-w-10 fa-7x"><path fill="currentColor" d="M41 288h238c21.4 0 32.1 25.9 17 41L177 448c-9.4 9.4-24.6 9.4-33.9 0L24 329c-15.1-15.1-4.4-41 17-41z" class=""></path></svg>
		.b-language__dropdown(v-if="langDropdown")
			button(@click="setLang('ru')").b-language__item
				i.e-lang-icon.e-lang-icon--ru
			button(@click="setLang('en')").b-language__item
				i.e-lang-icon.e-lang-icon--en
			button(@click="setLang('tr')").b-language__item
				i.e-lang-icon.e-lang-icon--tr
</template>

<script>
import i18n from '@/lang/lang'
export default {
	components: {
		i18n
	},
	data () {
		return {
			isActive: false,
			langDropdown: false,
			language: 'ru'
		}
	},
	methods: {
		toggleDropdown () {
			this.langDropdown = !this.langDropdown
			this.isActive = !this.isActive
		},
		setLang: function (lang) {
			document.querySelector('html').setAttribute('lang', lang)
			this.$store.dispatch('setLang', lang)
			this.langDropdown = !this.langDropdown
			this.language = lang
			this.isActive = !this.isActive
		}
	}
}
</script>
<style lang="sass">
	.b-language
		position: relative
		&__header
			position: relative
			z-index: 1
			cursor: pointer
			display: flex
			align-items: center
			&.active > i
					opacity: 0
		&__header-icon-sort
			color: $color-accent
			//font-size: 13px
			margin-left: .6rem
			svg
				width: 13px
				height: 13px
			&--dark
				color: $color-dark
		&__dropdown
			position: absolute
			top: -.3rem
			left: -.5rem
			z-index: 1
			padding: .5rem .5rem
			background-color: rgba(0,0,0, .3)
			+border-radius(.4rem)
			+transition(all, .4s, ease)
		&__item
			display: block
			height: auto
			margin-bottom: .4rem
			&:last-child
				margin-bottom: 0
</style>



