<template lang="pug">
	.b-main-form__input-block
		.b-main-form__input-focus
			input(:type="isType", :placeholder="placeholder", :value="value").b-main-form__input
</template>
<script>
export default {
	props: ['placeholder', 'value', 'type'],
	data () {
		return {
			isType: this.type || 'text'
		}
	}
}
</script>



