import Vue from 'vue'
import VueI18n from 'vue-i18n'

import ru from './ru.json'
import en from './en.json'
import tr from './tr.json'

Vue.use(VueI18n)

const locale = 'ru'

const messages = {
	ru,
	en,
	tr
}

const i18n = new VueI18n({
	/** 默认值 */
	locale,
	messages
})

export default i18n



