<template lang="pug">
	slick(ref="slick" :options="slickOptions").b-ipad-screen.js-merchant-ipad-slider
		.b-ipad-screen__item
			img(src="@/assets/img/business/screen-ipad.png" alt="screen")
		.b-ipad-screen__item
			img(src="@/assets/img/business/screen-ipad.png" alt="screen")
</template>
<script>
import Slick from 'vue-slick'
export default {
	components: {
		Slick
	},
	data () {
		return {
			slickOptions: {
				dots: false,
				arrows: false,
				vertical: true,
				verticalSwiping: true,
				slidesToShow: 1,
				slidesToScroll: 1,
				draggable: false
			}
		}
	}
}
</script>



